import { Component } from '@angular/core';
import { NavController, AlertController, IonicPage, Loading, LoadingController } from 'ionic-angular';
import { AuthService } from '../../providers/auth-service/auth-service';
import { HomePage } from '../home/home';

@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  createSuccess = false;
  registerCredentials = { email: '', password: '' };
  loading: Loading;
  constructor(private nav: NavController, private auth: AuthService, private alertCtrl: AlertController, private loadingCtrl: LoadingController) { }

  // ionViewCanEnter(): boolean {
  //   if(this.auth.loggedIn()){
  //     this.nav.setRoot(HomePage);
  //     return false;
  //   }
  //   else return true;
  // }

  public register() {
    this.showLoading();
    this.auth.register(this.registerCredentials).subscribe(data => {
      if (data.success) {
        this.createSuccess = true;
        this.showPopup("Success", data.msg);
      } else {
        this.showPopup("Error", data.msg);
      }
    },
      error => {
        this.showPopup("Error", error);
      });
  }

  showPopup(title, text) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: text,
      buttons: [
        {
          text: 'OK',
          handler: data => {
            if (this.createSuccess) {
              this.nav.popToRoot();
            }
          }
        }
      ]
    });
    alert.present();
  }

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }

}



